import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController, PopoverController, ToastController } from 'ionic-angular';

import { Storage } from '@ionic/storage';
import { SoapService } from '../soap.service';
import { api_base_url, api_user, api_pass, sc_code, api_p2b_url } from '../../config';
import { DatePipe } from '@angular/common';
import { P2bDetailPage } from '../p2b-detail/p2b-detail';
/**
 * Generated class for the P2bPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-p2b',
  providers: [SoapService],
  templateUrl: 'p2b.html',
})
export class P2bPage {
  fakeUsers: Array<any> = new Array(5);
  p2buser: any;
  year: any;
  month: any;
  monthNext: any;
  tgl: any;
  p2bList: Array<any> = [];
  userdataTPK: any;
  isAtasan: boolean;
  isLoadingBadges: boolean = true;
  isLoading: Boolean = true;
  badgesList: any;
  fromDate: any;
  toDate: any;
  checkDate: any;
  loading: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public soapService: SoapService,
    public loadingCtrl: LoadingController,
    public storage: Storage,
    public alertCtrl: AlertController,
    public popoverCtrl: PopoverController,
    public datepipe: DatePipe,
    public toastCtrl: ToastController
  ) {



  }

  ionViewDidEnter() {
    this.year = this.datepipe.transform(new Date(), 'yyyy');
    this.storage.get('userdataTPK').then((val) => {
      this.userdataTPK = val;
      this.getBadges();
      // if ( this.isEmptyObject(this.userdataTPK['data']['LISTOFFICER'][0]) && this.isEmptyObject(this.userdataTPK['data']['DATA_BAWAHAN'][0]) && this.isEmptyObject(this.userdataTPK['data']['DATA_BAWAHAN_TNO'][0]) ) {
      //   this.isAtasan = false;
      // } else {
      //   this.isAtasan = true;
      // }   
      // this.getP2bUser();   
      this.newSession('first', '');
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad P2bPage');
  }

  newSession(type, functionName) {
    if (type == 'first' && functionName == '') {
      this.isLoading = true;  
    }    
    // this.loading = this.loadingCtrl.create({
    //   spinner: 'dots',
    //   content: "Mohon Tunggu...",
    //   cssClass: 'transparent',
    //   dismissOnPageChange: true
    // });
    // this.loading.present();    
      this.soapService
        .post(api_base_url, 'eoffice_get_user_data', {
          fStream: JSON.stringify(
            {
              usernameEDI: api_user,
              passwordEDI: api_pass,
              username: this.userdataTPK['data']['NIPP'],
            }
          )
        }).then(result => {
          let responData = JSON.parse(String(result));          
          if (responData['rcmsg'] == "SUCCESS") {
            if (responData['data']['login_status'] == '404 Not Found') {              
            } else if (responData['data'] == undefined) {              
            } else if (responData['data']['login_status'] == 'AP NOT ALLOWED') {              
            }
            else {
              this.userdataTPK = responData;
              if (this.isEmptyObject(this.userdataTPK['data']['LISTOFFICER'][0]) && this.isEmptyObject(this.userdataTPK['data']['DATA_BAWAHAN'][0]) && this.isEmptyObject(this.userdataTPK['data']['DATA_BAWAHAN_TNO'][0])) {
                this.isAtasan = false;
              } else {
                this.isAtasan = true;
              }
              this.storage.set('userdataTPK', responData).then(() => {
                this.userdataTPK = responData;
                this.getP2bUser(type, functionName);
              });
            }
          } else {            
          }
        })
        .catch(error => {
          let toast = this.toastCtrl.create({
            message: 'Gagal mendapatkan data P2B, periksa koneksi internet anda.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
          // console.log(error);
        
          if (type == 'first' && functionName == '') {
            this.isLoading = false;  
          } else if (type == 'infinite' && functionName != '') {
            functionName.complete();
          } else if (type == 'refresh' && functionName != '') {
            functionName.complete();
          }
  
          this.isLoading = false;               
        });    
  }


  getList(type, functionName) {
    this.soapService
      .post(api_p2b_url, 'eoffice_p2b_eval', {
        fStream: JSON.stringify(
          {
            usernameEDI: api_user,
            passwordEDI: api_pass,
            p2b_id: this.p2buser['P2B_ID'],
            year: this.year
          }         
        )
      }).then(result => {
        var responData = JSON.parse(String(result));
        // console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          this.p2bList = [];
          if (responData['data'].length > 0) {
            for (let i = 0; i < responData['data'].length; i++) {
              this.p2bList.push(responData['data'][i]);
            }
          }          
        } else {
          let toast = this.toastCtrl.create({
            message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }
        // this.loading.dismiss();
        if (type == 'first' && functionName == '') {
          this.isLoading = false;  
        } else if (type == 'infinite' && functionName != '') {
          functionName.complete();
        } else if (type == 'refresh' && functionName != '') {
          functionName.complete();
        }        
      })
      .catch(error => {        
        let alert = this.alertCtrl.create({
          title: '',
          subTitle: 'Gagal Mendapatkan Data P2B, Periksa Koneksi Internet Anda.',
          buttons: ['OK']
        });                
        // this.loading.dismiss();
        this.isLoading = false;
        // console.log(error);
      });
  }

  getP2bUser(type, functionName) {
    this.soapService
      .post(api_p2b_url, 'eoffice_p2b_user', {
        fStream: JSON.stringify({
          usernameEDI: api_user,
          passwordEDI: api_pass,
          nipp: this.userdataTPK.data.NIPP          
        }          
        )
      }).then(result => {
        var responData = JSON.parse(String(result));
        // console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {          
          this.p2buser = responData['data'];
          this.setDateRule();
          this.getList(type, functionName);          
        } else {          
          let toast = this.toastCtrl.create({
            message: responData['rcmsg'],
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
          this.loading.dismiss();
        }
      })
      .catch(error => {        
        let toast = this.toastCtrl.create({
          message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();       
        // console.log(error);
      
        if (type == 'first' && functionName == '') {
          this.isLoading = false;  
        } else if (type == 'infinite' && functionName != '') {
          functionName.complete();
        } else if (type == 'refresh' && functionName != '') {
          functionName.complete();
        }

        this.isLoading = false;
      });
  }

  setDateRule() {    
    this.month = this.datepipe.transform(new Date(), 'M');    
    this.tgl = this.datepipe.transform(new Date(), 'dd');
    let currentYear = this.datepipe.transform(new Date(), 'yyyy');

    let tgl_awal = (this.p2buser['FL_AKHIR'] == true) ? this.p2buser['TGL_PALINGAKHIR_AKHIR'] : this.p2buser['TGL_PENGAJUAN_AWAL']
    let tgl_akhir = (this.p2buser['FL_AKHIR'] == true) ? this.p2buser['TGL_PALINGAKHIR_AKHIR'] : this.p2buser['TGL_PENGAJUAN_AKHIR']  

    if ( parseInt(this.tgl) >= 1 && parseInt(this.tgl) <= parseInt(tgl_akhir)) {
      
      if (this.month == 1) {
        this.month = '12';
        currentYear = (parseInt(currentYear) - 1).toString();        
      } else {
        this.month = (parseInt(this.month) - 1).toString();
      }
    }    
    
    let startDate = tgl_awal + '/' + this.month + '/' + currentYear;
    this.monthNext = (this.month == '12') ? 1 : parseInt(this.month) + 1;
    let yearEndDate = (this.month == '12') ? (parseInt(currentYear) + 1).toString() : currentYear;
    let endDate = tgl_akhir + '/' + this.monthNext + '/' + yearEndDate;
    let currentDate = this.datepipe.transform(new Date(), 'dd/M/yyyy'); 

    var sd = startDate.split("/");
    var ed = endDate.split("/");
    var cd = currentDate.split("/");

    this.fromDate = new Date(parseInt(sd[2]), parseInt(sd[1]) - 1, parseInt(sd[0]));  // -1 because months are from 0 to 11
    this.toDate = new Date(parseInt(ed[2]), parseInt(ed[1]) - 1, parseInt(ed[0]));
    this.checkDate = new Date(parseInt(cd[2]), parseInt(cd[1]) - 1, parseInt(cd[0]));            
  }

  goToDetail(data) {
    if (((this.checkDate >= this.fromDate && this.checkDate <= this.toDate) && data['Status'] != 'CLOSED') || data['Status'] == 'SAVED' || data['Status'] == 'EVALUATED SAVED' || data['Status'] == 'FINISHED' || data['Status'] == 'POSTED' || data['Status'] == 'EVALUATED') {
      this.navCtrl.push(P2bDetailPage, {
        userdataTPK: this.userdataTPK,
        p2bdata: data,
        p2buser: this.p2buser
      });
    } else {      
    }
  }

  getBadges() {
    // console.log(  {
    //   usernameEDI: api_user,
    //   passwordEDI: api_pass,
    //   iduser: this.userdataTPK['data']['IDUSER'],
    //   idjabatan: this.userdataTPK['data']['IDJABATAN'],
    //   nipp: this.userdataTPK['data']['NIPP']
    // });
    this.isLoadingBadges = true;
    this.soapService
      .post(api_p2b_url, 'eoffice_countbadges', { 
        fStream: JSON.stringify(
          {
            usernameEDI: api_user,
            passwordEDI: api_pass,
            iduser: this.userdataTPK['data']['IDUSER'],
            idjabatan: this.userdataTPK['data']['IDJABATAN'],
            nipp: this.userdataTPK['data']['NIPP']
          }
        )
      }).then(result => {
        var responData = JSON.parse(String(result));        
        console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          this.badgesList = responData['data'];          
        } else {
          let toast = this.toastCtrl.create({
            message: 'Gagal Mendapatkan Notifikasi.',
            duration: 3000,
            position: 'bottom'
          });          
        }
        this.isLoadingBadges = false;
      })
      .catch(error => {        
        let toast = this.toastCtrl.create({
          message: 'Gagal Mendapatkan Notifikasi, Periksa Koneksi Internet Anda.',
          duration: 3000,
          position: 'bottom'
        });        
        this.isLoadingBadges = false;
        // console.log(error);
      });
  }

  presentPopover(myEvent) {
    let popover = this.popoverCtrl.create("MenuP2bPage", {
      p2buser: this.p2buser,
      badges: this.badgesList != null ? this.badgesList['JUMLAH_RENCANA_P2B_BAWAHAN'] : ''
    }, { cssClass: "my-popover" });
    popover.present({
      ev: myEvent
    });
  }

  convertMonths(month) {
    switch (month) {
      case 'JANUARI':
        return "1";
      case 'FEBRUARI':
        return "2";
      case 'MARET':
        return "3";
      case 'APRIL':
        return "4";
      case 'MEI':
        return "5";
      case 'JUNI':
        return "6";
      case 'JULI':
        return "7";
      case 'AGUSTUS':
        return "8";
      case 'SEPTEMBER':
        return "9";
      case 'OKTOBER':
        return "10";
      case 'NOVEMBER':
        return "11";
      case 'DESEMBER':
        return "12";
      default:
        return "false";
    }
  }

  convertInt(val) {
    return parseInt(val);
  }

  isEmptyObject(obj) {
    for (var prop in obj) {
      if (obj.hasOwnProperty(prop)) {
        return false;
      }
    }
    return true;
  }

  parse(val) {
    var intValue = parseInt(val);

    if (intValue > 0) {
      return true;
    } else {
      return false;
    }
  }

  checkTgl() {

    if (this.checkDate >= this.fromDate && this.checkDate <= this.toDate) {
      return true;
    } else if (this.checkDate <= this.checkDate) {

    } else {
      return false;
    }
  }

  checkClosed(data) {  
    if (((this.checkDate >= this.fromDate && this.checkDate <= this.toDate) && data['Status'] != 'CLOSED') || data['Status'] == 'SAVED' || data['Status'] == 'EVALUATED SAVED' || data['Status'] == 'FINISHED' || data['Status'] == 'POSTED' || data['Status'] == 'EVALUATED') {  
      return true;
    } else {    
      return false;
    }
  }


}



